import Vue from 'vue'
import Vuex from 'vuex'
import firebase from 'firebase'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
	images:[],
  },
  mutations: {
	setImages(state, payload) {
      state.images = Object.values(payload);
    }
  },
  actions: {
	getImages({ commit }) {
      firebase
        .database()
        .ref("MENU_DUBAY")
        .get().then(function(snapshot) {
			if (snapshot.exists()) {
                commit("setImages", snapshot.val());
				console.log(snapshot.val());
            }
        })
		.catch(function(error) {
          console.error(error);
        });
    }
  },
  getters: {
	images: (state) => {
      return state.images;
    }
  },
});