<template>
  <img class="imgM card" :style="{width:width+'px',height:height+'px'}" :src="prop_image.src">
</template>

<script>
export default {
  name: 'ImageMain',
  props:{
    prop_image:{
      type: Object,
      default: function () {
        return {}
      }
	}
  },
  data(){
    return{
	width: 350,
	}
  },
  methods: {
    updateWidth() {
	if(window.innerWidth<750){
	this.width = window.innerWidth*0.45;
	}
    },
	onImageMain(){
	this.$emit('onimagemain');
	}
  },
  computed:{
    height(){
	return this.width*1.23
	},
  },
  created() {
    window.addEventListener('resize', this.updateWidth);
  },
}
</script>

<style scoped>
.imgM {
margin-bottom:20px;
margin-left:auto;
margin-right:auto;
cursor:pointer;
}
.imgM:hover {
border: 4px solid #caccc8;
}
.imgM:active {
border: 4px solid #caccc8;
}
</style>