import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../views/Main.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'main',
    component: Main
  },
  {
    path: "/breakfast",
    name: "breakfast",
    component: () => import("../views/Breakfast.vue")
  },
  {
    path: "/dishes",
    name: "dishes",
    component: () => import("../views/Dishes.vue")
  },
  {
    path: "/new",
    name: "new",
    component: () => import("../views/New.vue"),
  },
  {
    path: "/pizza",
    name: "pizza",
    component: () => import("../views/Pizza.vue"),
  },
  {
    path: "/rolls",
    name: "rolls",
    component: () => import("../views/Rolls.vue"),
  },
  {
    path: "/salads",
    name: "salads",
    component: () => import("../views/Salads.vue"),
  },
  {
    path: "/soups",
    name: "soups",
    component: () => import("../views/Soups.vue"),
  }
]

const router = new VueRouter({
  mode: "history",
  routes
})

export default router
